<template>
	<div class="ReplenishArea">
		<header><i class="iconfont icon-xiangzuo1" @click="toReplenishIndex"></i><span>补货-区域/库位</span><i class="iconfont icon-saoma" @click="scan"></i></header>
		<div class="container">
			<div>
				<span>补货区域</span>
				<input  placeholder="请输入内容" @keyup.enter="getFocus('LibraryPosition')"   @change="toUppercase('area')"    @focus="g_focus('area')" ref="area"  v-model="area" ></input>
				<i v-if="area" @click="clearInput('area')" class="iconfont icon-cuowu"></i>
			</div>
			<div>
				<span>补货库位</span>
				<input  placeholder="请输入内容"  ref="LibraryPosition"  @focus="g_focus('LibraryPosition')"   @change="toUppercase('LibraryPosition')"   @keyup.enter="submit"   v-model="LibraryPosition"></input>
				<i v-if="LibraryPosition" @click="clearInput('LibraryPosition')"  class="iconfont icon-cuowu"></i>
			</div>	
			<span  class="g_singleBtn"   @click="submit">确认</span>
		</div>
		<div v-if="tipsHide"  @touchmove.prevent  class="g_tips">
			<div>
				<span>错误信息</span>
				<span>{{msg}}</span>
				<p   @click='tipsHideen(inputFocus)'>确定</p>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"ReplenishArea",
		data(){
			return{
				area:'',//补货区域
				LibraryPosition:"",//补货库位
				tipsHide:false,//提示框是否出现
				msg:"",//提示信息
				inputFocus:"",
			}
		},
		mounted() {
			this.$refs['area'].focus();
			window.QRScanAjax = this.QRScanAjax;
			$("input").attr("maxlength","50");
		},
		created(){
			// 缓存补货区域
			if(this.$store.state.Replenish_area){

				this.area = this.$store.state.Replenish_area;
			}
		},
		methods:{
			// 接收iOS/android值
			QRScanAjax:function (qrcode){

				var inputName = this.$store.state.ScanQRCodeInputName;
				this[inputName] = qrcode;

				// 补货区域
				if(inputName == "area") {

					this.getFocus('LibraryPosition');

				// 补货库位
				}else if(inputName == "LibraryPosition") {

					this.submit();
				}
			},
			toReplenishIndex:function(){

				//跳转到 补货首页 页面
				this.$router.push({
					name:"ReplenishIndex",
					query:{
					}
				})
			},
			submit:function(){

				var that = this;

				if(!(this.area || this.LibraryPosition )){
					
					that.msg = "请至少填写一个信息";

					that.inputFocus = "area";

					that.tipsShow("LibraryPosition");

					return;
				}

				var ReplenishAreaUrl = this.HOST + "RFRepCalculator.do";

				var r_postData = {
					RepMode:that.$store.state.ReplenishMode,
					PutawayZone : this.area,
					Loc : this.LibraryPosition
				}

				this.httpRequest(ReplenishAreaUrl,r_postData,function(res){

					if(res.data.data.length){

						that.$store.state.Replenish_TaskData = res.data.data;
						that.$store.state.Replenish_area = that.area;

						//跳转到 任务列表 页面
						that.$router.push({
							name:"ReplenishTaskList",
							query:{
							}
						})

					}else{

						that.tipsShow("area");

						that.inputFocus = "area";

						that.msg = "无需补货";
					}
				},"area")
			}
		}
	}
</script>
<style lang="less" scoped>
.ReplenishArea{
	&>.container{
		input::placeholder{
			font-size: 25/50rem;
		}
		input{
			font-size: 25/50rem;
		}
		&>div{
			width:100%;
			height: 80/50rem;
			padding: 25/50rem 20/50rem 0;
			box-sizing: border-box;
			position: relative;
			span{
				
				width: 140/50rem;
				text-align: right;
			}

			input{
				margin-left: 10/50rem;
				width:490/50rem;	
				height:55/50rem;
				padding:0 10/50rem;
			}
			i{
				position:absolute;
				right:90/50rem;
				font-size: 30/50rem;
				top: 36/50rem;
			}
		}
		&>div:nth-child(2){
			margin-bottom: 100/50rem;
		}
	}
}
	@media screen and (max-width: 330px){
		.ReplenishArea{
			&>.container{
				input::placeholder{
					font-size: 35/50rem;
				}
				input{
					font-size: 35/50rem;
				}
				&>div{
					height: 80/50rem;
					input{
						height:65/50rem;
					}
					i{
						right:65/50rem;
						font-size: 40/50rem;
						top: 36.5/50rem;
					}
				}
			}
		}
	}
	@media screen and (max-width: 250px){
		.ReplenishArea{
			&>.container{
				input::placeholder{
					font-size: 40/50rem;
				}
				input{
					font-size: 40/50rem;
				}
				&>div{
					height: 90/50rem;
					span{
						width: 120/50rem;
					}
					input{
						margin-left: 0/50rem;
						width:450/50rem;	
						height:70/50rem;
					}
					i{
						font-size: 45/50rem;
						top: 38/50rem;
					}
				}
			}
		}
	}
</style>


